@import url('./normalize.css');
@import url('./card.style.css');
@import url('./containers.style.css');
@import url('./btnBanner.style.css');
@import url('./sitem.style.css');

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
:root{
  --lime:#00ff00;
  --azurra:#668cff;
  --blu:#0039e6;
  --orange:orange;
  --rosso:#ff0000;
  --gs-light:#e6e6e6;
  --gs-mid:#808080;
  --gs-dark:#333333;
  --gs-react:#282c34;
  --radius:8px;
}

main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width:100%;
  margin:0 auto;
}

.glass{
  background: var(--gs-light);
  top:96px;
  width:80%;
  background: linear-gradient(to right bottom, 
  rgba(255,255,255,0.3),
  rgba(255,255,255,0.7));
  border-radius: 2rem;
}

/*Headings*/
h1{
 
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.25em;
}
h2{
  padding: 10px 5px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.25em;
}
h3{
  font-size: 1.5em;
  color:var(--orange);
  text-decoration: underline;
  margin-bottom: 12px;
}


.rowrBanner{
  background-color: var(--gs-light);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin: auto;
}


.justifyRight{
  text-align: right;
  border:2px solid lime;
}
.cartOuter{
  text-align: center;
}
.cartInner{
 display: block;
}
.cartInner i{
  font-size: 10em;
}
.hashCenter{
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
ul {
  list-style-type: none;
  
}

li {
  color:var(--gs-light);
  text-align: left;
  padding-left: .8em;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.25em;
  letter-spacing: 0.15em;
}
li::first-letter{
  text-transform: capitalize;
  line-height: 1em;
  font-weight: 400;
}
.banner{
  text-align: center;
  background-color: var(--gs-react);
  color:var(--gs-light);
}
.outer{
background-color: var(--gs-react);
padding:12px 24px;
text-align: center;
}
.inner{
display: flex;
flex-direction: row;
align-items:center;
justify-content:space-evenly;
}
.column{
  display: flex;
  flex-direction: column;
  flex:25%;
  justify-content:center;
  align-self: center;
}
.column img{
  margin-top: 8px;
  vertical-align: middle;
  
}
.column div{
  padding: 12px 24px;
  height: 20vh;
  display:grid;
  place-items: center;
  filter: opacity(0.95) ;
}
.column h5{
  letter-spacing: 0.125em;
  font-weight: 700;
  font-size: xx-large;
}
.rowr .column:nth-child(1){
  border-right: 2px solid var(--blu);
}

.rowr{
  display:flex;
  max-width: 80%;
  flex-direction: row;
  flex-wrap:wrap;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width:768px) {
  .rowr .column:nth-child(1){
    border-right:none;
    border-top: 2px solid var(--blu);
  }
 .inner{
  padding:2em 4em;
  flex-direction:column-reverse;
 }
 .outer{
  /*
  background: url('./nicolas-picard-JjBQLWs2UPA-unsplash.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  */
 }
  .bigFontsize{
    margin-top: 12px;
    margin-bottom: 12px;
    font-size: 1.25em;
  }
  .firstLetter{
    float: left;
    text-transform: uppercase;
    line-height: 0.5;
    font-size: 1.5em;
  }
  .firstLetter::first-letter{
    float: left;
    text-transform: uppercase;
    line-height: 0.35;
    font-size: 1.5em;
  }
  .centerBanner{
    font-size: 2em;
  }
}

.bannerTxt{
  color: var(--amarello);
}

.blu{
  color:var(--blu);
}
.textAlign{
  text-align: center;
  max-width:50%;
  margin: 24px;
}
.textAlign h3{
  letter-spacing: 0.25em;
  text-decoration: none;
}
.inlineBlock{
  display: inline-block;
}
.iconBiglime{
  font-size: xx-large;
  color: var(--lime);
}
.iconBigrosso{
  font-size: xx-large;
  color: var(--rosso);
}
.iconApp{
  padding: 24px;
  font-size: 192px;
  color:var(--orange);
}